.habit {
  position: relative;
  border-radius: 5px;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 30px;
}

.habit-delete {
  position: absolute;
  right: 3px;
  top: 3px;
  font-size: 12px;
  padding: 1px 5px;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  transition: transform 0.2s ease-out;
  cursor: default;
}

.habit-delete:hover {
  transform: scale(1.1);
}

.habit-header {
  margin-top: 20px;
  display: flex;
  align-items: center;
}

.habit-title {
  font-size: 22px;
  width: 540px;
  box-sizing: border-box;
  padding: 5px 10px;
}

.habit-color {
  margin-left: 20px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.habit-color-button {
  font-size: 22px;
  padding: 8px;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  transition: transform 0.2s ease-out;
  cursor: default;
}

.habit-color-button:hover {
  transform: scale(1.1);
}

.habit-color-picker {
  position: absolute;
  top: 42px;
  transition: transform 0.2s ease-out;
  overflow: hidden;
  transform-origin: top right;
  z-index: 1;
}

.habit-color-picker-closed {
  transform: scale(0);
}

.habit-color-picker-open {
  transform: scale(1);
}

.habit-repeat {
  display: flex;
  justify-content: center;
  font-size: 16px;
  margin: 25px 0 15px 0;
}

.habit-repeat-day {
  margin: 0 10px;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  padding: 5px 8px;
  transition: transform 0.2s ease-out;
  cursor: default;
}

.habit-repeat-day:hover {
  transform: scale(1.1);
}

.habit-repeat-icon {
  margin-left: 6px;
}
