@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 100vw;
  height: 100vh;
  --font-family: 'Open Sans Condensed';
  font-family: var(--font-family);
  --main-color: 32, 32, 32;
  --shadow-color: 150, 150, 150;
  --light-color: 200, 200, 200;
  color: rbg(var(--main-color));
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
  margin-top: 30px;
  padding: 30px 40px;
  position: relative;
  min-width: 450px;
}

.container-header {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 45px;
  margin-bottom: 35px;
}

.container-header > * {
  margin: 0 20px;
}

.link-button, button {
  font-family: var(--font-family);
  outline: none;
  display: inline-block;
  background-color: transparent;
  border: 1px solid rgb(var(--main-color));
  border-radius: 10px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}
.link-button, .link-button:link, .link-button:visited, .link-button:hover, .link-button:focus, .link-button:active{
  text-decoration: none;
  color: rgb(var(--main-color));
}
.link-button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
button {
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}
button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.plain-link, .plain-link:link, .plain-link:visited, .plain-link:hover, .plain-link:focus, .plain-link:active{
  text-decoration: none;
  color: rgb(var(--main-color));
}

.nav-link {
  font-size: 25px;
  width: 40px;
  text-align: center;
}

input[type="text"] {
  font-family: var(--font-family);
  outline: none;
  color: rgb(var(--main-color));
  background-color: white;
  border: 1px solid #cdcdcd;
  border-radius: 10px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
}
::-webkit-input-placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}
::-moz-placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}
:-ms-input-placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}
::-ms-input-placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}
::placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}

.calendar-button {
  position: absolute;
  top: 0;
  left: 0;
  padding: 13px 17px;
  font-size: 18px;
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}
.calendar-button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.user-list {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.user-link {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 35px;
  background-color: white;
}

.create-user-form {
  margin-top: 20px;
}

.create-user {
  padding: 10px 20px;
  font-size: 35px;
  font-weight: bold;
  color: white;
  background-color: rgb(var(--shadow-color));
}

.username-input {
  margin: 10px;
  padding: 10px 20px;
  font-size: 35px;
}


.calendar-day-date {
  text-align: center;
}

.calendar-day-weekday {
  font-size: 30px;
  text-align: center;
}

.calendar-day-habit {
  display: flex;
  align-items: center;
  border-radius: 5px;
  margin-top: 20px;
  font-size: 22px;
}

.calendar-day-habit-title {
  margin: 20px 30px 20px 20px;
  min-width: 350px;
}

.calendar-day-icon {
  margin-left: auto;
  margin-right: 20px;
}
.calendar-header {
  display: flex;
  align-items: center;
  font-size: 40px;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
  margin: 20px 0;
  padding: 2px;
}

.calendar-header > * {
  margin: 0 10px;
}

.habit-list-btn {
  margin-left: auto;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
     -moz-column-gap: 10px;
          column-gap: 10px;
  grid-row-gap: 10px;
  row-gap: 10px;
}

.calendar-month-day {
  width: 160px;
  height: 110px;
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}

.calendar-month-day:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.previous-month {
  background-color: rgb(var(--light-color));
}

.next-month {
  background-color: rgb(var(--light-color));
}

.today {
  border: 3px solid rgb(var(--main-color));
  box-shadow: 0px 0px 10px rgb(var(--shadow-color));
}

.calendar-month-day-date {
  position: absolute;
  top: 0;
  right: 5px;
  margin: 5px;
  font-size: 20px;
}

.calendar-month-day-habit {
  flex: 1 1;
}

.calendar-year-month {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  font-size: 30px;
}

.calendar-year-progress {
  width: 500px;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
  overflow: hidden;
  margin-left: 10px;
}

.calendar-year-progress-bar {
  height: 100%;
  background-color: rgb(var(--shadow-color));
}
.create-habit {
  display: block;
  font-size: 22px;
  padding: 10px;
  width: 120px;
  margin: 30px auto 0 auto;
}
.habit {
  position: relative;
  border-radius: 5px;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 30px;
}

.habit-delete {
  position: absolute;
  right: 3px;
  top: 3px;
  font-size: 12px;
  padding: 1px 5px;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
  cursor: default;
}

.habit-delete:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.habit-header {
  margin-top: 20px;
  display: flex;
  align-items: center;
}

.habit-title {
  font-size: 22px;
  width: 540px;
  box-sizing: border-box;
  padding: 5px 10px;
}

.habit-color {
  margin-left: 20px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.habit-color-button {
  font-size: 22px;
  padding: 8px;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
  cursor: default;
}

.habit-color-button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.habit-color-picker {
  position: absolute;
  top: 42px;
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
  overflow: hidden;
  -webkit-transform-origin: top right;
          transform-origin: top right;
  z-index: 1;
}

.habit-color-picker-closed {
  -webkit-transform: scale(0);
          transform: scale(0);
}

.habit-color-picker-open {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.habit-repeat {
  display: flex;
  justify-content: center;
  font-size: 16px;
  margin: 25px 0 15px 0;
}

.habit-repeat-day {
  margin: 0 10px;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  padding: 5px 8px;
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
  cursor: default;
}

.habit-repeat-day:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.habit-repeat-icon {
  margin-left: 6px;
}

