@import url('https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap');

.App {
  width: 100vw;
  height: 100vh;
  --font-family: 'Open Sans Condensed';
  font-family: var(--font-family);
  --main-color: 32, 32, 32;
  --shadow-color: 150, 150, 150;
  --light-color: 200, 200, 200;
  color: rbg(var(--main-color));
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
  margin-top: 30px;
  padding: 30px 40px;
  position: relative;
  min-width: 450px;
}

.container-header {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 45px;
  margin-bottom: 35px;
}

.container-header > * {
  margin: 0 20px;
}

.link-button, button {
  font-family: var(--font-family);
  outline: none;
  display: inline-block;
  background-color: transparent;
  border: 1px solid rgb(var(--main-color));
  border-radius: 10px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
  transition: transform 0.2s ease-out;
}
.link-button, .link-button:link, .link-button:visited, .link-button:hover, .link-button:focus, .link-button:active{
  text-decoration: none;
  color: rgb(var(--main-color));
}
.link-button:hover {
  transform: scale(1.1);
}
button {
  transition: transform 0.2s ease-out;
}
button:hover {
  transform: scale(1.1);
}

.plain-link, .plain-link:link, .plain-link:visited, .plain-link:hover, .plain-link:focus, .plain-link:active{
  text-decoration: none;
  color: rgb(var(--main-color));
}

.nav-link {
  font-size: 25px;
  width: 40px;
  text-align: center;
}

input[type="text"] {
  font-family: var(--font-family);
  outline: none;
  color: rgb(var(--main-color));
  background-color: white;
  border: 1px solid #cdcdcd;
  border-radius: 10px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
}
::placeholder {
  color: rgb(var(--shadow-color));
  font-style: italic;
}

.calendar-button {
  position: absolute;
  top: 0;
  left: 0;
  padding: 13px 17px;
  font-size: 18px;
  transition: transform 0.2s ease-out;
}
.calendar-button:hover {
  transform: scale(1.1);
}
