.calendar-header {
  display: flex;
  align-items: center;
  font-size: 40px;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
  margin: 20px 0;
  padding: 2px;
}

.calendar-header > * {
  margin: 0 10px;
}

.habit-list-btn {
  margin-left: auto;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  column-gap: 10px;
  row-gap: 10px;
}
