.calendar-year-month {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  font-size: 30px;
}

.calendar-year-progress {
  width: 500px;
  border: 1px solid rgb(var(--main-color));
  border-radius: 5px;
  box-shadow: 0px 0px 2px rgb(var(--shadow-color));
  overflow: hidden;
  margin-left: 10px;
}

.calendar-year-progress-bar {
  height: 100%;
  background-color: rgb(var(--shadow-color));
}